<template>
    <div class="manager_iframe">

        <form class="layui-form" action="" id="List">
          <div class="layui-form-item">
            <input type="text" name="title" required  lay-verify="required" placeholder="" autocomplete="off" maxlength="20" class="layui-input">
          </div>
          <div class="layui-form-item">
            <input type="text" name="path"  placeholder="" autocomplete="off" class="layui-input">
          </div>
          <div class="layui-form-item">
            <input type="text" name="author" required  lay-verify="required" placeholder="" autocomplete="off" maxlength="10" class="layui-input">
          </div>


          <div class="layui-form-item" id="editor">
            <p></p>
          </div>

          <div class="layui-form-item">
            <button class="layui-btn layui-btn-primary layui-btn-fluid" lay-submit lay-filter="formDemo"></button>
          </div>
        </form>
    </div>
</template>

<script>
  import wangEditor from "../../static/editor/wangEditor.min.js"
    export default {
        name: "Edit",
      mounted(){
        var E = wangEditor
        var editor = new E('#editor')
        editor.customConfig.pasteFilterStyle = false
        editor.customConfig.pasteIgnoreImg = false
        //  var editor = new E( document.getElementById('editor') )
        editor.create()
        layui.use(['form','jquery'], function(){
          var form = layui.form;
          var $ =layui.jquery;

          //
          form.on('submit(formDemo)', function(data){
            console.log(data);
            let date = new Date().toLocaleDateString();
            data.field.date=date;
            data.field.content=editor.txt.html();
            $.post('/manager/artinsert',data.field,(data)=>{

              if (!data.status){
                layer.msg('!');
                document.getElementById("List").reset();
                editor.txt.clear();
                // setTimeout(()=> window.open('/manager/',"_self"),1000) ;
              }else{
                layer.msg('!');
              }

            });
            // layer.msg(JSON.stringify(data.field));
            return false;
          });
        });
      }
    }
</script>




